import React, { useCallback, useMemo } from 'react';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import Close from '../../images/svg/close-button.svg';
import Isvg from 'react-inlinesvg'
export const CarouselImage = ({ source, handleCarousel }) => {

    const settings = useMemo(() => ({
        showThumbs: false,
        showIndicators: false,
        showStatus: false
    }), [])

    const closeCarousel = useCallback(() => {
        handleCarousel(false);
    }, [])

    return (
        <div className="carousel-popup">
            <button className="close" onClick={closeCarousel}> <Isvg src={Close} /> </button>
            <Carousel {...settings}>
                {source.map((item, _idx) => {
                    return (
                        <img src={item} alt="Carousel" key={_idx} />
                    )
                })}
            </Carousel>

        </div>
    )
}