import React, { useCallback, useEffect, useRef, useState } from 'react';
import mapIcon from '../../images/map_icon_v2.svg';

export const MapComponent = (props) => {
    const ref = useRef(null);
    const [state, setState] = useState({})

    const initMap = useCallback(() => {
        setState({ _mapInit: true });
        var latLng = new window.google.maps.LatLng(props.latLng.Lat, props.latLng.Lng);

        var map = new window.google.maps.Map(ref.current, {
            zoom: 10,
            center: latLng,
            mapTypeId: window.google.maps.MapTypeId.ROADMAP,
            disableDefaultUI: true,
            gestureHandling: "gestureHandling",

        });
      // eslint-disable-next-line
        var marker = new window.google.maps.Marker({
            position: latLng,
            map: map,
            icon: mapIcon
        });

    }, [props])

    useEffect(() => {
        if (ref.current) {
            initMap();
        }
    }, [initMap, props, state._mapInit])

    return (
        <div>
            {props.mapinit &&
                <div className="map" ref={(input) => { ref.current = input; }}>
                </div>
            }
        </div>
    );
}